<template>
  <v-text :config="textConfig" @click="clickType"/>
</template>

<script>
export default {
  props: ['data'],
  data: () => ({
    textConfig : {}
  }),
  mounted() {
    this.text();
    if(this.data.properties != undefined){
      if(this.data.properties.radius != undefined){
        this.circleText();
      }
      if(this.data.properties.sides != undefined){
        this.polyText();
      }
    }
  },
  methods: {
    basicText(){
      this.textConfig['fontSize']       = this.data.text.fontSize;
      this.textConfig['fontFamily']     = this.data.text.fontFamily;
      this.textConfig['fill']           = this.data.text.color;
      this.textConfig['align']          = this.data.text.align;
      this.textConfig['verticalAlign']  = this.data.text.verticalAlign;
      this.textConfig['text']           = this.data.text.text;
    },
    circleText(){      
      // circle text
      this.textConfig = {};
      this.textConfig['x']              = this.data.x-this.data.properties.radius;
      this.textConfig['y']              = this.data.y-this.data.properties.radius;
      this.textConfig['width']          = this.data.properties.radius*2;
      this.textConfig['height']         = this.data.properties.radius*2;
      this.basicText();
    },
    polyText(){
      // poly text
      this.textConfig = {};
      this.textConfig['x']              = this.data.x-this.data.width/2;
      this.textConfig['y']              = this.data.y-this.data.height/2;
      this.textConfig['width']          = this.data.width;
      this.textConfig['height']         = this.data.height;
      this.basicText();
    },
    text(){
      this.textConfig = {};
      this.textConfig['x']              = this.data.x;
      this.textConfig['y']              = this.data.y;
      this.textConfig['width']          = this.data.width;
      this.textConfig['height']         = this.data.height;
      this.basicText();
    },
    clickType(){
      if(this.data.action){
        var link = this.data.action.link;
        if( typeof link == "string" ){
          window.open(link,'_blank')
        }if( typeof link == "number"){
          this.$eventBus.$emit('Pick',link);
        }
      }else{
        console.log("action empty")
      }
    }
  },
  watch: {
  },
}
</script>

