<template>
  <v-layout>
    <v-stage ref="stage" :config="stageSize" style="position:absolute; border:1px solid black;">
      <v-layer>
        <div v-if="getItems.shapes">
          <div v-for="(item,i) in getItems.shapes" :key="`${item.id}-${i}`">
          <dynamic-konva-link :type="item.ContentsType" :data="item"/>
          </div>
        </div>
        <div v-if="getItems.texts">
          <div v-for="(item,i) in getItems.texts" :key="`${item.id}-${i}`">
            <dynamic-konva-link :type="item.ContentsType" :data="item"/>
          </div>
        </div>
        <div v-if="getItems.images">
          <div v-for="(item,i) in getItems.images" :key="`${item.id}-${i}`">
            <dynamic-konva-link :type="item.ContentsType" :data="item"/>
          </div>
        </div>
      </v-layer>
    </v-stage>
    <div v-if="getItems.tables">
      <div v-for="(item,i) in getItems.tables" :key="`${item.id}-${i}`" style="position:absolute">
        <dynamic-konva-link :type="item.ContentsType" :data="item"/>
      </div>
    </div>
    <div v-if="getItems.charts">
      <div v-for="(item,i) in getItems.charts" :key="`${item.id}-${i}`">
        <dynamic-konva-link :type="item.ContentsType" :data="item"/>
      </div>
    </div>
  </v-layout>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
const width = 1300;
const height = 750;

export default {
  data: () => ({
    stageSize: {
      width: width,
      height: height
    },
    // contents: [],
    // shapes: [],
    // tables: [],
    // charts: [],
    // texts : [],
    // textPolys : [],
    // images: [],
  }),
  computed:{
    ...mapState({
      konva:"konva"
    }),
    ...mapGetters(["getItems"])
  },
  created(){
    //this.$eventBus.$on('slidePickKonva',this.pick);
  },
  methods:{
    // eventBus를 통한 데이터 처리
    pick(item){
      this.contents = item;
      this.shapes = [];
      this.tables = [];
      this.charts = [];
      this.texts  = [];
      this.textPolys  = [];
      this.images = [];
      this.NameFilter();
    },
    NameFilter(){
      var masterData = this.contents.children;      

      // shape 감지 shape Data 저장
      var shapeDataFilter = masterData.filter(function (item) {
        return item.ContentsType == "rect" || item.ContentsType == "circle" || item.ContentsType == "regularPolygon"
      });
      if( shapeDataFilter.length != 0){
        this.shapes = shapeDataFilter;
      }

      var copyShapes = JSON.parse(JSON.stringify(shapeDataFilter));
      var shapesText = copyShapes.filter(item => {
        item.ContentsType = "text";
        return item.text != null;
      })
      if( shapesText.length != 0){
        this.texts = shapesText;
      }

      // Text 감지 Text Data 저장
      var textFilter = masterData.filter(function (item) {
        return item.ContentsType == "text"
      });
      if( textFilter.length != 0){
        textFilter.forEach(element => {
          this.texts.push(element);
        });
      }

      // Image 감지 Image Data 저장
      var imageDataFilter = masterData.filter(item => {
        return item.ContentsType == "image"
      });
      if( imageDataFilter.length != 0){
        this.images = imageDataFilter;
      }

      // Table 감지 Table Data 저장
      var tableDataFilter = masterData.filter(function (item) {
        return item.ContentsType == "table"
      });
      if( tableDataFilter.length != 0){
        this.tables = tableDataFilter;
      }

      // chart 감지 chart Data 저장
      var chartDataFilter = masterData.filter(function (item) {
        return item.ContentsType == "chart"
      });
      if( chartDataFilter.length != 0){
        this.charts = chartDataFilter;
      }
    }
  }
}
</script>

<style>
.table{
  height:100%;
}
</style>
