/* eslint-disable no-undef */
import Vue  from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

Vue.prototype.$isAuth = true;

// 페이지 권한 설정
Vue.prototype.$priv = {};

export default new Vuex.Store({
  state: {
    accessToken: Vue.prototype.$isAuth,
    setRoute: localStorage.getItem('setRoute'),
    userId: "sccho",
    password: "12345",
    konva:{
      page:0,
      items:{
        shapes: [],
        tables: [],
        charts: [],
        texts : [],
        textPolys : [],
        images : []
      },
    },
    priv:[
      { to:'/3_UserGroup', priv:'r'}
    ]
  },
  actions: {
    login({commit}){
      commit('actLogin')
    },
    logout({commit}){
      commit('actLogout')
    },
    route({commit},data){
      commit('actRoute',data);
    },
    konvaItem({commit},item){
      commit('actKonvaItem',item);
    }
  }, 
  getters: {
    getLogin (state) {
      return (state.accessToken == false || state.accessToken === '') ? false : true;
    },
    getItems (state){
      return state.konva.items;
    },
    getPriv (state){
      return state.priv;
    }
  },
  mutations: {
    actLogin(state){
      Vue.prototype.$isAuth = true;
      Vue.prototype.$priv = {
        Leaflet: false,
        VueLayer_basic: true,
        iframe: false
      };
      localStorage.clear();
    },
    actLogout(state){
      Vue.prototype.$isAuth = false;
      localStorage.clear();
    },
    actRoute(state,data){
      localStorage.setItem('setRoute', data);
    },
    actKonvaItem(state, item){
      state.konva.page = item.value;
      
      state.konva.items.shapes = [];
      state.konva.items.tables = [];
      state.konva.items.charts = [];
      state.konva.items.texts = [];
      state.konva.items.textPolys = [];
      state.konva.items.images = [];

      var masterData = item.children;      

      // shape 감지 shape Data 저장
      var shapeDataFilter = masterData.filter(function (item) {
        return item.ContentsType == "rect" || item.ContentsType == "circle" || item.ContentsType == "regularPolygon"
      });
      if( shapeDataFilter.length != 0){
        state.konva.items.shapes = shapeDataFilter;
      }

      var copyShapes = JSON.parse(JSON.stringify(shapeDataFilter));
      var shapesText = copyShapes.filter(item => {
        item.ContentsType = "text";
        return item.text != null;
      })
      if( shapesText.length != 0){
        state.konva.items.texts = shapesText;
      }

      // Text 감지 Text Data 저장
      var textFilter = masterData.filter(function (item) {
        return item.ContentsType == "text"
      });
      if( textFilter.length != 0){
        textFilter.forEach(element => {
          state.konva.items.texts.push(element);
        });
      }

      // Image 감지 Image Data 저장
      var imageDataFilter = masterData.filter(item => {
        return item.ContentsType == "image"
      });
      if( imageDataFilter.length != 0){
        state.konva.items.images = imageDataFilter;
      }

      // Table 감지 Table Data 저장
      var tableDataFilter = masterData.filter(function (item) {
        return item.ContentsType == "table"
      });
      if( tableDataFilter.length != 0){
        state.konva.items.tables = tableDataFilter;
      }

      // chart 감지 chart Data 저장
      var chartDataFilter = masterData.filter(function (item) {
        return item.ContentsType == "chart"
      });
      if( chartDataFilter.length != 0){
        state.konva.items.charts = chartDataFilter;
      }
    },
  }
});