<template>
  <v-table
    v-bind:class="data.name"
    :height="data.height"
    :width="data.width"
    :columns="data.properties.columns"
    :title-rows="data.properties.titleRows"
    :table-data="data.properties.tableData"
    row-hover-color="#eee"
    row-click-color="#edf7ff"
  ></v-table>
</template>

<script>
import $ from "jquery";

export default {
  props: ['data'],
  data: () => ({
  }),
  created(){    
  },
  mounted() {
    // CSS 변경
    this.tableCSS();
  },
  methods: {
    tableCSS(){
      $('.'+this.data.name).css({
        'margin-left': this.data.x+'px',
        'margin-top': this.data.y+'px',
        'position': 'absolute'
      });
    }
  },
  watch: {
  },
  updated() {
  },
}
</script>

<style>
.canvas {
  border: 1px solid #000;
}
</style>
