<template>
  <v-regular-polygon :config="rectConfig" @click="clickType"/>
</template>

<script>
export default {
  props: ['data'],
  data: () => ({
    rectConfig : {}
  }),
  mounted() {
    var dataBox = {};
    dataBox['x']      = this.data.x;
    dataBox['y']      = this.data.y;
    dataBox['width']  = this.data.width;
    dataBox['height'] = this.data.height;
    dataBox['fill']   = this.data.properties.color;
    dataBox['sides']   = this.data.properties.sides;
    this.rectConfig = dataBox;
  },
  methods: {
    clickType(){
      if(this.data.action){
        var link = this.data.action.link;
        if( typeof link == "string" ){
          window.open(link,'_blank')
        }if( typeof link == "number"){
          this.$eventBus.$emit('Pick',link);
        }
      }else{
        console.log("action empty")
      }
    }
  },
  watch: {
  },
}
</script>

