<template>
  <canvas id="myCanvas" :width="width" :height="height" v-on:click="clickType()"></canvas>
</template>

<script>
const width = window.innerWidth;
const height = window.innerHeight;

  export default {
    props: ['data'],
    data: () => ({
      width: width,
      height: height
    }),
    mounted() {
      // mounted 이후부터 canvas의 DOM에 접근할 수 있습니다.         
      this.draw()
    },
    methods: {
      draw() {
        var x = this.data.x;
        var y = this.data.y;
        var color = this.data.color;
        var radius = this.data.radius;

        var c = document.getElementById("myCanvas");
        var ctx = c.getContext("2d");
        ctx.beginPath()
        // this.ctx.clearRect(0, 0, 200, 200)
        // ctx.rect(10, 10, 50, 50)
        ctx.arc(x, y, radius, 0, Math.PI * 2)
        ctx.fillStyle = color;
        ctx.fill()
      },
      clickType(){
        console.log("hi");
      }
    },
    watch: {
      radius() {
        this.draw(this.layout.Radius)
      }
    },
  }
</script>

<style>
.canvas {
  border: 1px solid #000;
}
</style>
