<template>
  <v-rect :config="{
    fillPatternImage: image,
    fillPatternScaleX: width?data.width/width:1,
    fillPatternScaleY: height?data.height/height:1,
    width: data.width? data.width:width,
    height: data.height? data.height:height,
    x: data.x,
    y: data.y,
  }" @click="clickType"/>
</template>

<script>
export default {
  props: ['data'],
  data: () => ({
    image: null,
    imageConfig:{},
    width: 1,
    height: 1
  }),
  created() {
    var image = new window.Image();
    image.onload = () => {
      this.image = image;
      this.width = image.width;
      this.height = image.height;
    };
    image.src = this.data.properties.src;
  },
  mounted() {
  },
  methods: {
    clickType(){
      if(this.data.action){
        var link = this.data.action.link;
        if( typeof link == "string" ){
          window.open(link,'_blank')
        }if( typeof link == "number"){
          this.$eventBus.$emit('Pick',link);
        }
      }else{
        console.log("action empty")
      }
    }
  },
  watch:{
    data(value){
      var image = new window.Image();
      image.onload = () => {
        this.image = image;
        this.width = image.width;
        this.height = image.height;
      };
      image.src = this.data.properties.src;
    }
  }
}
</script>

<style>
.canvas {
  border: 1px solid #000;
}
</style>
