<template>
  <v-container class="pa-0 ma-0">
    <v-layout>
      <v-flex fill-height xs-12>
        <canvas class="canvas" ref="canvas" v-on:click="clickType()"></canvas>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    props: ['layout'],
    data: () => ({
    }),
    mounted() {
      // mounted 이후부터 canvas의 DOM에 접근할 수 있습니다.
      // CreateJS 등을 사용할 때에도 여기에서 canvas를 매개 변수로 전달해야 합니다.
      
      // this.$refs.canvas.width = document.documentElement.clientWidth;
      
      this.ctx = this.$refs.canvas.getContext('2d')
      this.$refs.canvas.width = window.innerWidth-18;
      this.$refs.canvas.height = window.innerHeight-110;
      this.draw(this.layout.Radius)
    },
    methods: {
      draw(radius) {
        this.ctx.beginPath()
        // this.ctx.clearRect(0, 0, 200, 200)
        this.ctx.rect(10, 10, 50, 50)
        this.ctx.arc(150, 150, radius, 0, Math.PI * 2)
        this.ctx.fill()
      },
      clickType(){
        console.log("hi");
      }
    },
    watch: {
      radius() {
        this.draw(this.layout.Radius)
      }
    },
  }
</script>

<style>
.canvas {
  border: 1px solid #000;
}
</style>
