import 'babel-polyfill'
import Vue from 'vue'
import App from './App.vue'
import router from './plugins/router'
import store from './plugins/store'
import VueToastr2 from 'vue-toastr-2'
import VeeValidate from 'vee-validate'
import { LMap, LTileLayer, LMarker, LPolygon, LCircle, LPolyline, LRectangle } from 'vue2-leaflet'
import { Icon } from 'leaflet'
import 'leaflet/dist/leaflet.css'
import 'leaflet.icon.glyph'
import './plugins/vuetify'
import VueKonva from 'vue-konva'
import AnimateCss from 'animate.css'
import 'tui-grid/dist/tui-grid.css'
import { Grid } from '@toast-ui/vue-grid'
import KakaoLogin from 'vue-kakao-login'
import { AgGridVue } from "ag-grid-vue"
import VueHtml2Canvas from 'vue-html2canvas'
import LiquorTree from 'liquor-tree'
import VueDaumPostcode from "vue-daum-postcode"
import './plugins/axios';
import './plugins/weather_icon/css/weather-icons.css';
import VueVideoPlayer from 'vue-video-player'
import 'video.js/dist/video-js.css'

Vue.use(VueVideoPlayer) 
/* {
  options: global default options,
  events: global videojs events
} */


// daum 주소찾기
Vue.use(VueDaumPostcode);

// kakao oath2
Vue.component("KakaoLogin", KakaoLogin);

// c3 style
import 'c3/c3.min.css';
import 'c3/c3.min.js';

// vue_easy_table
import './plugins/easytable/themes-base/index.css';
import {VPagination, VTable } from './plugins/easytable';

Vue.component("v-pagination", VPagination);
Vue.component("v-table", VTable);

// vuelayer
import { Map, TileLayer, OsmSource, Geoloc, Feature, PointGeom ,PolygonGeom } from 'vuelayers'
import 'vuelayers/lib/style.css' // needs css-loader

Vue.use(Map)
Vue.use(TileLayer)
Vue.use(OsmSource)
Vue.use(Geoloc)
Vue.use(Feature)
Vue.use(PointGeom)
Vue.use(PolygonGeom)

// component
import './components';

Vue.config.productionTip = false

// toastr2
import 'vue-toastr-2/dist/vue-toastr-2.min.css';
window.toastr = require('toastr');
Vue.use(VueToastr2);

// veeValidate
Vue.use(VeeValidate);

// Leaflet
Vue.component('l-map', LMap);
Vue.component('l-tile-layer', LTileLayer);
Vue.component('l-marker', LMarker);
Vue.component('l-polygon', LPolygon);
Vue.component('l-circle', LCircle);
Vue.component('l-polyline', LPolyline);
Vue.component('l-rectangle', LRectangle);

delete Icon.Default.prototype._getIconUrl;

Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});

// vue konva
Vue.use(VueKonva);

// Animatie.css url-https://github.com/daneden/animate.css
Vue.use(AnimateCss)

// toast-ui-vue-grid
Vue.component('grid', Grid);

// ag_grid
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
Vue.component("ag-grid-vue", AgGridVue);

// LiquorTree
// global registration
Vue.use(LiquorTree)
Vue.component(LiquorTree.name, LiquorTree)

// vuehtml2canvas 
Vue.use(VueHtml2Canvas);

// 각 components 에서 this.$eventBus.$emit('key', 'value'); 이벤트 발행
// this.$eventBus.$on('key', 'functionName'); 이벤트 구독, created or mounted 때 등록
// this.$eventBus.$off('key'); 이벤트 구독해제, beforeDestory 때 등록
Vue.prototype.$eventBus = new Vue();

// eslint-disable-next-line
/* eslint-disable */
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
