<template>	
	<component :is="component" :data="data"/>
</template>

<script>
	export default
		{
			name: 'link',
			props: ['data', 'type'],
			data()
			{
				return {
					component: null,
				}
			},
			computed: {
				loader()
				{
					if (!this.type)
					{
						return null
					}
					return () => import(`./contents/${this.type}`)
				},
			},
			mounted()
			{
				this.loader().then(() =>
				{
					this.component = () => this.loader();
				}).catch(() =>
				{
					
				})
			},
		}
</script>

