<template>
  <div id="chart"></div>
</template>

<script>
import c3 from 'c3';
import $ from "jquery";

export default {
  props: ['data'],
  data: () => ({
    chart: []
  }),
  created(){    
  },
  mounted() {
    // CSS 변경
    // var chart = c3.generate({
    //   bindto: '#chart',
    //   data: {
    //     columns: [
    //       ['data1', 30, 200, 100, 400, 150, 250],
    //       ['data2', 50, 20, 10, 40, 15, 25]
    //     ],
    //     axes: {
    //       data2: 'y2' // ADD
    //     }
    //   },
    //   axis: {
    //     y2: {
    //       show: true // ADD
    //     }
    //   }
    // });
    this.NewChart();
  },
  methods: {    
    NewChart(){
      // 기존 Chart 데이터 삭제
      this.chart = [];
      
      var chartData = this.data

      // Name 생성
      var chartName = chartData.name;

      // chartName 앞에 c3_ 추가 = chartName 맨 앞에 숫자가 오게되면 c3차트를 그릴 수 없음
      // Html 생성
      var $createChart = $('<div class='+'c3_'+chartName+'></div>');
      var createChart = document.createElement('div');
      $createChart.appendTo($('#chart'));
      
      // CSS 변경
      $(`.c3_`+chartName).css({
        'max-width': chartData.width,
        'margin-left': chartData.x+'px',
        'margin-top': chartData.y+'px',
        'z-index': chartData.setZIndex,
        'position': 'absolute'
      });
      
      var chartProperties = {
        bindto: `.c3_`+chartName, // bind id 입력
        size: {
          width: chartData.width,
          height: chartData.height
        },
        data: {
          columns: chartData.properties.data.columns,
          type: chartData.properties.chartType,
          types: chartData.properties.chartTypes,
          axes: chartData.properties.axes,
          groups: chartData.properties.groups
          // labels: true
        },
        pie: {
          label: {
              format: function (value, ratio, id) {
                var ratioPer = ratio*100;
                var ratioResult = ratioPer.toPrecision(3);
                  return value+` /\n`+ratioResult+`(%)`;
              }
          }
        },
        donut: {
          // title 입력시 차트 중심에 입력가능
          title: '',
          label: {
            format: function (value, ratio) {
              var ratioPer = ratio*100;
              var ratioResult = ratioPer.toPrecision(3);
              var dataPush = value+` /\n`+ratioResult+`(%)`;
              return dataPush;
            }
          }
        },
        color: {
          pattern: chartData.properties.color
        },
        axis:{
          x:{
            type: 'category',
            // tick name rotate option
            tick: chartData.properties.axisTick,
            height: chartData.properties.legendHeight
          },
          y2:{
            show: chartData.properties.showY2
          }
        },
        grid:{
          x: {
            show: chartData.properties.gridX,
            lines: chartData.properties.gridXLines
          },
          y: {
            show: chartData.properties.gridY,
            lines: chartData.properties.gridYLines
          }
        },
        legend: chartData.properties.legend,
        zoom:{
          enabled: true
        }
      }
      // Chart 생성
      var newChart = c3.generate(chartProperties);

      // this.charts에 Name값 추가
      newChart.name = chartName;

      // Chart Data Push
      this.chart.push(newChart);
        
    },
    ChartCSS(){
      // chart legend font size resizing
      if(this.chartData.length > 0){
        if(this.chartData[0].attrs.legendFontSize !== undefined || this.chartData[0].attrs.legendFontSize !== null){
          // legend title css 변경
          // $('.c3-legend-item').css('font-size','20px');
          // Chart legend css 변경
          $('.c3 svg').css({
            'font-size':this.chartData[0].attrs.legendFontSize+'px'
          });
        }
      }
    }
  },
  watch: {
  },
  updated() {
  },
}
</script>

<style>
.c3{
  position: absolute !important;
}
</style>
