<template>
  <v-app>
    <router-view/>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  components: {
  },
  data () {
    return {
    }
  },
  mounted(){
    if(this.isLogin){
      this.$router.push('/');
    }else{
      this.$router.push('/sign');
    }
  },
  computed: {
    isLogin: function() {
      return this.$store.getters.getLogin;
    }
  }
}
</script>
<style>
html{
  overflow-y: auto !important;
}
#app {
  font-family: 'Noto Sans Korean', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 12px;
  word-break: keep-all;
  overflow-y: auto;
}
</style>
