<template>
  <v-circle :config="circleConfig" @click="clickType"/>
</template>

<script>
export default {
  props: ['data'],
  data: () => ({
    circleConfig:{}
  }),
  mounted() {
    var dataBox = {};
    dataBox['x']      = this.data.x;
    dataBox['y']      = this.data.y;
    dataBox['radius'] = this.data.properties.radius;
    dataBox['fill']   = this.data.properties.color;
    this.circleConfig = dataBox;
  },
  methods: {
    clickType(){
      console.log("clickType Circle");
    }
  },
  watch: {
  },
}
</script>

<style>
.canvas {
  border: 1px solid #000;
}
</style>
