router.beforeEach((to, from, next)=>{
  // 처음 접근시 로그인 페이지로 push
  // if(Vue.prototype.$isAuth)to('/');next();  

  // store.getters에서 priv가져오는 방법
  let storePriv = store.getters.getPriv;
  // console.log("storePriv",storePriv);

  // prototype변수를 통해 priv를 가져오는 방법
  var userPriv = Vue.prototype.$priv;

  var privName = true;
  storePriv.map(element => {
    if(element.to == to.path){
      privName = false;
    }
  });

  if(privName){
    // console.log("routing success:'"+to.path+"'");
    next();
  }else{
    window.alert('권한이 없습니다.');
    // 한단계 뒤로 보내기
    router.go(-1);
    return 
    // next({ path: '/sign' });
  }
})


export default router;