<template>
  <v-layout>
    <!-- svg를 그릴땐 앞에 DIV가 붙으면 안된다 -->
    <svg :width="width" :height="height">
      <dynamic-svg-link v-for="(item,index) in layout" :key="index" :type="item.ContentsType" :data="item"/>
    </svg>
  </v-layout>
</template>

