<template>
  <v-navigation-drawer
    v-model="drawer"
    fixed
    app
    width="250"
  >
    <v-layout column align-center>
      <v-flex>
        <img class="logo_img" :src="require('@/assets/logo.png')"/>
      </v-flex>
    </v-layout>
    <v-list dense class="menu">
      <v-list-group
        v-for="item in items"
        :key="item.title"
        v-model="item.active"
        no-action
        class="drawer"
      >
        <template v-slot:activator>
          <v-list-tile>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>

        <v-list-tile
          v-for="subItem in item.subItems"
          :key="subItem.title"
          :to="subItem.to"
        >
          <v-list-tile-action>
            <v-icon>{{ subItem.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ subItem.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>

import {mapGetters} from 'vuex';

export default {
  data: () => ({
    drawer: true,
    items: [
      {
        title: '1_ Leaflet',
        active: true,
        subItems: [
          { title: '1_Leaflet_GeoJson' ,  to: '/1_Leaflet' ,          icon: 'map'},
          { title: '2_basic' ,            to: '/2_Leaflet_basic' ,    icon: 'add_box' },
          { title: '3_polygon' ,          to: '/3_Leaflet_polygon' ,  icon: 'add_box' },
          { title: '4_click' ,            to: '/4_Leaflet_click' ,    icon: 'add_box' },
          { title: '5_geofence' ,         to: '/5_Leaflet_geofence' , icon: 'add_box' }
        ]
      },
      {
        title: '2_ VueLayer',
        subItems: [
          { title: '1_Vuelayer_basic' ,   to: '/1_Vuelayer_basic' ,     icon: 'map'},
          { title: '2_VueLayer_geofence' ,to: '/2_VueLayer_geofence' ,  icon: 'add_box' },
          { title: '3_UserGroup' ,        to: '/3_UserGroup' ,          icon: 'add_box' }
        ]
      },
      {
        title: '3_ html',
        subItems: [
          { title: '1_iframe' ,         to: '/1_iframe' ,           icon: 'desktop_mac'},
          { title: '2_fileRead' ,       to: '/2_fileRead' ,         icon: 'add_box'},
          { title: '3_grid_table' ,     to: '/3_grid_table' ,       icon: 'add_box'},
          { title: '4_grid_complex' ,   to: '/4_grid_complex' ,     icon: 'add_box'},
          { title: '5_vue_easyTable' ,  to: '/5_vue_easyTable' ,    icon: 'add_box'},
          { title: '6_ag_grid',         to: '/6_ag_grid' ,          icon: 'add_box'},
          { title: '7_liquorTree',      to: '/7_liquorTree' ,       icon: 'add_box'},
          { title: '8_daum_post',       to: '/8_daumPostcode' ,     icon: 'add_box'},
          { title: '9_openWeatherMap',  to: '/9_openWeatherMap' ,   icon: 'add_box'},
          { title: '10_connectOsCheck', to: '/10_connectOsCheck' ,  icon: 'add_box'},
          { title: '11_openWeatherMap_iocatcher', to: '/11_openWeatherMap_iocatcher' ,  icon: 'add_box'},
          { title: '12_videoTest',      to: '/12_videoTest' ,       icon: 'add_box'},
        ]
      },
      {
        title: '4_ shape_img_render',
        subItems: [
          { title: '1_konva_basic'   , to: '/1_konva_basic'    , icon: 'desktop_mac'},
          { title: '2_konva_image'   , to: '/2_konva_image'    , icon: 'add_box' },
