          { title: '4_html5_dynamic' , to: '/4_html5_dynamic'  , icon: 'code' },
          { title: '5_svg_dynamic'   , to: '/5_svg_dynamic'    , icon: 'code' },
          { title: '6_canvas_dynamic', to: '/6_canvas_dynamic' , icon: 'code' },
          { title: '7_es2015'        , to: '/7_es2015'         , icon: 'code' }
        ]
      },
      {
        title: '5_ Animation',
        subItems: [
          { title: '1_animation_basic' ,    to: '/1_animation_basic' ,    icon: 'desktop_mac'},
          { title: '2_animation_jquery' ,   to: '/2_animation_jquery' ,   icon: 'add_box'},
          { title: '3_vue_animate' ,        to: '/3_vue_animate' ,        icon: 'add_box'},
          { title: '4_animation_velocity' , to: '/4_animation_velocity' , icon: 'add_box'},
        ]
      },
      {
        title: '6_ Login',
        subItems: [
          { title: '1_google' ,   to: '/1_google' ,   icon: 'desktop_mac'},
          { title: '2_kakao' ,    to: '/2_kakao' ,    icon: 'add_box'}
        ]
      },
      {
        title: '7_ Vue',
        subItems: [
          { title: '1_mixin' ,        to: '/1_mixin' ,          icon: 'desktop_mac'},
          { title: '2_callFunction' , to: '/2_callFunction' ,   icon: 'add_box'},
          { title: '3_variableBar' ,  to: '/3_variableBar' ,    icon: 'add_box'},
        ]
      }
    ]
  }),
  computed: {
  },
  methods:{
    drawerToggle(){
      this.drawer = !this.drawer;
    }
  },
  created() {
    this.$eventBus.$on('drawer', () =>{ this.drawerToggle() });
  }
}
</script>

<style>
.logo_img{
  width: 100%;
  padding: 4rem;
}
.menu{
  width: 100%;
}
.v-list__tile {
  padding: 0 25px !important;
}
.v-list__tile__title {
  font-weight: 550 !important;
}
.theme--light.v-list{
  color: #8f2c35;
}
.theme--light.v-icon {
  color: #8c8c8c;
}
.drawer .primary--text {
  color: #3692EE !important;
  background: #ededed !important;
  border-right: 3px solid #3692EE !important;
}
.v-list__group__items--no-action .v-list__tile {
  padding-left: 40px !important;
}
</style>
