<template>
  <canvas id="myCanvas" :width="width" :height="height"/>
</template>

<script>
import $ from "jquery";
const width = window.innerWidth;
const height = window.innerHeight;

export default {
  props: ['data'],
  data: () => ({
    width: width,
    height: height
  }),
  mounted() {
    console.log("data",this.data);    
    this.draw();
  },
  methods: {
    draw() {
      var x = this.data.x;
      var y = this.data.y;
      var width = this.data.width;
      var height = this.data.height;
      var color = this.data.color;
        
      var c = document.getElementById("myCanvas");
      var ctx = c.getContext("2d");
      ctx.beginPath();
      ctx.rect(x, y, width, height);
      ctx.fillStyle = color;
      ctx.fill();
      
      var t = c.getContext("2d");
      t.beginPath();
      t.rect(500, 500, width, height);
      t.strokeStyle = color;
      t.stroke();
    },
    clickType(){
      console.log("hi");
    }
  },
  watch: {
  },
}
</script>

<style>
.rec {
  width: 100px;
  height: 100px;
  background: red;
}
</style>
